              <div class="list-item-content">
                <div class="list-item-content-title">{{news[2].title}}</div>
                <div class="list-item-bottom">
                  <div class="list-item-bottom-topic">{{news[2].bottomPartTitile}}</div>
                  <div class="list-item-bottom-date">{{ news[2].bottomPartDate}}</div>
                </div>
              </div>
            </div>
            <!-- //item -->
            <div class="news-list-item" @click="changedata(3)">
              <div class="list-item-img-container">
                <img class="list-img" :src="news[3].photo" alt />
              </div>
              <div class="list-item-content">
