  padding: 10px;
  -webkit-box-shadow: 4px 6px 23px -3px rgba(0, 0, 0, 0.33);
  -moz-box-shadow: 4px 6px 23px -3px rgba(0, 0, 0, 0.33);
  box-shadow: 4px 6px 23px -3px rgba(0, 0, 0, 0.33);
}

#news-left-part-outter-container {
