  /* border-radius: 25%; */
  /* background: chartreuse; */
  overflow: hidden;
}

.list-img {
  width: 100%;
  border-radius: 10px;
 height: 100%;

  overflow: hidden;
  /* width: auto; */
}

.list-item-content {
     width: 61%;
  position: relative;
  margin-left: 1.5%;
}
.list-item-content-title{
    font-size: 12px;
}
.list-item-bottom {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  margin-bottom: 2%;
  /* padding: 10px 0px; */
}

.list-item-bottom-topic {
  float: left;
  font-size: 10px;
  margin: 0 8px;
  padding-left: 5px;
  border-left: 2px solid #890404;
}

.list-item-bottom-date {
  float: right;
  font-size: 8px;
  margin: 0 8px;
  padding-right: 5px;
  color: rgb(169, 172, 173);
  font-style: oblique;
}
</style>


