<style>
#chart-outer-container {
  display: flex;

  justify-content: center;
  align-items: center;
  height: 300px;
  margin: 50px 0;
}
#chart-inner-container {
  width: 60%;
  height: 100%;
  /* display: flex;
  justify-content: center;
  align-items: center; */
  border-radius: 20px;
  padding: 10px;
  -webkit-box-shadow: 4px 6px 23px -3px rgba(0, 0, 0, 0.33);
  -moz-box-shadow: 4px 6px 23px -3px rgba(0, 0, 0, 0.33);
  box-shadow: 4px 6px 23px -3px rgba(0, 0, 0, 0.33);
}

/*                    FIRST GRID */

